*{
    margin:0;
    padding: 0;

}
body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
body::-webkit-scrollbar{
    display: none;
}
.rowPost{
    margin-left: 20px;
    color: #fff;
}
.row{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.row::-webkit-scrollbar{
    display: none;
}
.postersm{
    max-height: 150px;
    margin-right: 10px;    
    transition: -webkit-transform .3s;    
    transition: transform .3s;    
    transition: transform .3s, -webkit-transform .3s;
    cursor: pointer;
}
.poster{
    max-height: 250px;
    margin-right: 10px;
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    cursor: pointer;
}
.poster:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.postersm:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.container{
	max-width: 1170px;
	margin:auto;
}

ul{
	list-style: none;
}
.footer{
	background-color: black;
    padding: 5px 0;
}
.footer-col{
   width: 25%;
   padding: 0 15px;
}
.footer-col h4{
	font-size: 18px;
	color: #ffffff;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
}
.footer-col h4::before{
	content: '';
	position: absolute;
	left:0;
	bottom: -10px;
	background-color: #E50914;
	height: 2px;
	box-sizing: border-box;
	width: 50px;
}
.footer-col ul li:not(:last-child){
	margin-bottom: 10px;
}
.footer-col ul li p{
	font-size: 16px;
	text-transform: capitalize;
	color: #ffffff;
	text-decoration: none;
	font-weight: 300;
	color: #bbbbbb;
	display: block;
	transition: all 0.3s ease;
}
#Disclaimer{
    color: #bbbbbb;
}
.footer-col ul li p:hover{
	color: #ffffff;
	padding-left: 8px;
}
.footer-col .social-links p{
	display: inline-block;
	height: 40px;
	width: 40px;
	background-color: rgba(255,255,255,0.2);
	margin:0 10px 10px 0;
	text-align: center;
	line-height: 40px;
	border-radius: 50%;
	color: #ffffff;
	transition: all 0.5s ease;
}
.footer-col .social-links a:hover{
	color: #24262b;
	background-color: #ffffff;
}

/*responsive*/
@media(max-width: 767px){
  .footer-col{
    width: 50%;
    margin-bottom: 30px;
}
}
@media(max-width: 574px){
  .footer-col{
    width: 100%;
}
}





